#footer-div {
    height: 10%;
    padding-top: 5px;
    display: flex;
    justify-content: center;
  }
  
  #footer-div a {
    padding: 10px;
    text-decoration: none;
    font-size: 50px;
    color: black;
  }
  
  #footer-div a:hover {
    transition: 0.5s;
    color: var(--tertiary);
  }